.activeQuiz {
  padding: 10px;
  margin: 20px auto;
  max-width: 600px;
  width: 100%;
  border: 2px solid white;
  border-radius: 10px;
}

.question {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
  color: white;
}

.question small {
  max-width: 50px;
  width: 100%;
}
